import Vue from '../../node_modules/vue/dist/vue.common.js'

const app = new Vue({
    el: "#app",
    data: {
        piano: [1,1.5,2,2.5,3,3.5,4,4.5,5,5.5,6,6.5,7,8,8.5,9,9.5,10,11,11.5,12,12.5,13,13.5,14,15],
        notes: [
            {"num":1,"time":150},
            {"num":1,"time":265},
            {"num":5,"time":380},
            {"num":5,"time":501},
            {"num":6,"time":625},
            {"num":6,"time":748},
            {"num":5,"time":871},
            {"num":4,"time":1126},
            {"num":4,"time":1247},
            {"num":3,"time":1365},
            {"num":3,"time":1477},
            {"num":2,"time":1597},
            {"num":2,"time":1714},
            {"num":1,"time":1837}
        ],
        //現在彈奏的位置
        now_step: 0,
        //下一個音彈奏的位置
        next_step: 0,
        //開始彈奏的時間
        play_time: 0,
        //開始錄音的時間
        record_time: 0,
        //現在在按什麼鍵
        now_key: -1,
        player: null,
        recorder: null,
        keyboard:[
            {num: 1,   key: 90 ,type: 'white'},
            {num: 1.5, key: 83 ,type: 'black'},
            {num: 2,   key: 88 ,type: 'white'},
            {num: 2.5, key: 68 ,type: 'black'},
            {num: 3,   key: 67 ,type: 'white'},
            {num: 4,   key: 86 ,type: 'white'},
            {num: 4.5, key: 71 ,type: 'black'},
            {num: 5,   key: 66 ,type: 'white'},
            {num: 5.5, key: 72 ,type: 'black'},
            {num: 6,   key: 78 ,type: 'white'},
            {num: 6.5, key: 74 ,type: 'black'},
            {num: 7,   key: 77 ,type: 'white'},
            {num: 8,   key: 81 ,type: 'white'},
            {num: 8.5, key: 50 ,type: 'black'},
            {num: 9,   key: 87 ,type: 'white'},
            {num: 9.5, key: 51 ,type: 'black'},
            {num: 10,  key: 69 ,type: 'white'},
            {num: 11,  key: 82 ,type: 'white'},
            {num: 11.5,key: 53 ,type: 'black'},
            {num: 12,  key: 84 ,type: 'white'},
            {num: 12.5,key: 54 ,type: 'black'},
            {num: 13,  key: 89 ,type: 'white'},
            {num: 13.5,key: 55 ,type: 'black'},
            {num: 14,  key: 85 ,type: 'white'},
            {num: 15,  key: 73 ,type: 'white'}
        ]
    },
    methods:{
        play: function(id){
            if(id > 0){
                //每次播放都從0開始
                $("audio[data-num='"+id+"']")[0].currentTime = 0;
                $("audio[data-num='"+id+"']")[0].play();
            }
        },
        play_next: function(){
            //定義現在播到第幾個音
            var now_num = this.notes[this.now_step].num;
            //執行播放(現在在第幾步,聲音大小)
            this.play(now_num);
            //再接續執行步數+1
            this.now_step += 1;
            //如果步數大於整個樂譜(播完整個樂譜)
            if(this.now_step >= this.notes.length){
                clearInterval(this.player);
                this.play_time = 0;
                this.stop_play();
            }
        },
        start_play_slow: function(){
            this.now_step = 0;
            this.play_time = 0;
            this.next_step = 0;
            this.player = setInterval(function(){
                if(app.notes.length > 0){
                    if(app.play_time >= app.notes[app.next_step].time){
                        app.play_next();
                        app.next_step += 1;
                    }else{
                        app.play_time += 1/2;
                    }
                }
            },2)
        },
        start_play: function(){
            this.now_step = 0;
            this.play_time = 0;
            this.next_step = 0;
            this.player = setInterval(function(){
                if(app.notes.length > 0){
                    if(app.play_time >= app.notes[app.next_step].time){
                        app.play_next();
                        app.next_step += 1;
                    }else{
                        app.play_time += 1;
                    }
                }
            },2)
        },
        stop_play: function(){
            clearInterval(app.player);
            this.now_step = 0;
            this.play_time = 0;
            this.next_step = 0;
        },
        start_record: function(){
            app.notes = [];
            this.record_time = 0;
            this.recorder = setInterval(function(){
                app.record_time += 1;
            })
        },
        stop_record: function(){
            clearInterval(this.recorder);
            this.record_time = 0;
        },
        //判斷我按下去的鍵是不是跟我按的值一樣
        play_now: function(id,key){
            if(this.now_key == key){
                return true;
            }
            var cur_id = this.now_step - 1;
            if (cur_id < 0 ){
                cur_id = 0;
            }
            var cur_text = this.notes[cur_id].num;
            if (cur_text==id){
                return true;
            } 
            return false

        },
        add_notes: function(id){

            //如果我正在錄音
            if(this.record_time > 0){
                this.notes.push({
                    num: id,
                    time: this.record_time
                })
            }
            this.play(id);
        },
        load_star: function(){
            $.ajax({
                url: 'https://awiclass.monoame.com/api/command.php?type=get&name=music_star',
                success: function(item){
                    app.notes = JSON.parse(item);
                }
            })
        },
        load_totoro: function(){
            $.ajax({
                url: 'https://awiclass.monoame.com/api/command.php?type=get&name=music_dodoro',
                success: function(item){
                    app.notes = JSON.parse(item);
                }
            })
        }
    }
});


//使用鍵盤操作
$(document).ready(function(){
    //當我按下鍵盤時
    $(window).keydown(function(event){
        //我按下的那個鍵是幾號
        let target = event.which;
        app.now_key = target;
        //掃描全部
        for(var i=0 ; i<app.keyboard.length ; i++){
            //如果發現我按下的那個鍵的號碼 有對應到 我keyboard裡面的號碼
            if(target == app.keyboard[i].key){
                //就播放(該號碼)
                app.add_notes(app.keyboard[i].num);
            }
        } 
    })

    $(window).keyup(function(){
        app.now_key = -1;
    })
});
